<template>
    <view class="text" :class="{'text-edit': !readOnly}">
        <u-textarea
        v-model="data" 
        :type="type"
        v-if="!readOnly"
        :custom-style="option"
        :maxlength="maxlength"
        placeholder="请输入文本"
        confirmType=''
        row="3"
        autoHeight/>
        
        <view v-else class="read-only">
            <text user-select selectable>
                {{data}}
            </text>
        </view>
    </view>
</template>


<script>
	export default {
        options: {  
            styleIsolation: 'shared'  
        },
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
            readOnly: {
                type: Boolean,
                default: {},
            },
        },
		data() {
			return {
				data: undefined,
				type: 'textarea',
                maxlength: -1
			}
		},
        computed: {

        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                immediate: true,
                handler(e) {
                    this.data = e
                },
            },
        },
	}
</script>
<style lang="scss">
    .text ::v-deep.u-textarea__field {
        font-size: unset !important;
        color: unset !important;
    }
    .text ::v-deep.u-textarea {
        border: none;
    }
    .text-edit .u-textarea textarea {
        min-height: 240rpx;
    }
    .text ::v-deep.u-textarea--disabled {
        background-color: #FFF !important;
    }
    .read-only{
        user-select: text !important;
    }
</style>